 // import Component from 'vue-class-component'
import {
   State,
   Getter,
   Action,
   Mutation,
   namespace
 } from 'vuex-class'

import {OrbitFS} from '../agent/fs';

 @Component
export default class Petition extends Vue {
   title="";
   body="";
   @State fs;


   async create() {
     await this.fs.write('/petitions/' + this.title, JSON.stringify({
       title: this.title,
       body: this.body
     }), {create: true});
     this.fs.commit();

   }


}



